<template>
    <section id="player">
        <div class="wrapper">
        <header class="header">
            <h1>简易音乐播放器</h1>
            <input
                    autofocus="autofocus"
                    autocomplete="off"
                    placeholder="请输入歌名"
                    class="new-todo"
                    v-model="inputValue"
                    @keyup.enter="search"
            />
        </header>
        <section class="main">
            <ul class="music-list">
                <li class="music" v-for="(item,index) in results" :key="index">
                    <div class="view">
                        <span class="index">{{index+1}}.  </span>
                        <label>{{item.name}}</label>
                        <button @click="play(item.id)" class="play"></button>
                    </div>
                </li>
            </ul>
        </section>
        <footer class="footer">
            <audio :src="url" autoplay controls class="video"></audio>
        </footer>
        </div>
    </section>
</template>
<script>
    import './style/player.css'
    import axios from 'axios'

    export default {
        data() {
            return {
                inputValue: "",
                results: [],
                url:""
            }
        },
        methods: {
            play(id) {
                axios({
                    url: "https://autumnfish.cn/song/url",
                    method: "get",
                    params: {
                        id
                    }
                }).then(res=>{
                    // console.log(res);
                    this.url = res.data.data[0].url
                })
            },
            search() {
                axios({
                    url: "https://autumnfish.cn/search",
                    method: "get",
                    params: {
                        keywords: this.inputValue
                    }
                }).then(res => {
                    this.results = res.data.result.songs
                })
            }
        }
    }
</script>
